import React from "react";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { members, speakers } from "../../../people";
import { volunteers } from "../../../people/volunteers/volunteers";
import TeamMember from "./teamMember";
import * as styles from "./team.module.css";

const OurTeam: React.FC = () => {
  const { t } = useTranslation();

  return (
    <div className={`page ${styles.team}`} id={"team"}>
      <h1 className={`title ${styles.title}`}>{t("title")}</h1>
      <div className={styles.list}>
        {members.map((member, index) => (
          <TeamMember key={"teamMember" + index} member={member} />
        ))}
      </div>

      <h2 className={styles.subtitle}>{t("other")}</h2>
      <div className={styles.list}>
        {speakers.map((member, index) => (
          <TeamMember key={"pastMember" + index} member={member} />
        ))}
      </div>

      <h2 className={styles.subtitle}>{t("volunteers")}</h2>
      <div className={styles.list}>
        {volunteers.map((member, index) => (
          <TeamMember key={"volunteer" + index} member={member} />
        ))}
      </div>
    </div>
  );
};

export default OurTeam;
